import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import FlightDetail from './simple/FlightDetail';
import { useState, useEffect } from 'react';
import FlightModal from './simple/FlightModal2';
import Pagination from 'react-bootstrap/Pagination';
//import { authToken } from '../utility/Constants';
// //generated from https://www.mockaroo.com/14dc7090
// const names = require("../data/names3.json");

import fields from "../data/fields.json";
import flightsFromFile from "../data/flights.json";

const  dailiesBaseUrl = "https://localhost:5001/v1/api/Dailies";

const rollOn = 600;
const rollOff = -30;



function addMinutes(date, minutes) {
    return new Date(date.getTime() + minutes*60000);
}

function FlightsSimple() {
    console.log("Using simple APIs V1");
    const [show, setShow] = useState(false);
    const [modalFlight, setModalFlight] = useState({});
    const [currentPage, setCurrentPage] = useState(1);
    //use flightsFromFile here if you want to work offline
    //const [flights, setFlights] = useState(flightsFromFile);
    const [flights, setFlights] = useState([]);

    let itemsPerPage = 50;
    let start = (currentPage - 1) * itemsPerPage;
    //let currentNames = names.slice(start, start + itemsPerPage);
    //let numPages = flights.length === 0 ? 1 : Math.floor((names.length - 1) / itemsPerPage) + 1;
    let numPages = 1;

    const getRecords = () => {
        console.log("Fetching data");
        const url = `${dailiesBaseUrl}/GetFlightsByTimeRange?rollOn=${rollOn}&rollOff=${rollOff}&maxRecords=${itemsPerPage}`;
        console.log("Fetching URL: " + url);
        fetch(url, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json' // Include if sending JSON data
            // Add other headers as needed
          }
        })
        .then(response => {
            if (!response.ok) {
              throw new Error(`HTTP error! Status: ${response.status}`);
            }
            return response.json();
          })
          .then(data => {
            console.log('Data:', data);
            // Handle the data
            //setRows(data);
            setFlights(data);
          })
          .catch(error => {
            console.error('Error fetching data:', error);
            // Handle errors
          });
    }

    useEffect(() => {
        getRecords();

      }, []
    );

    const handleClose = () => setShow(false);


    function ShowFlight(flight){
      setModalFlight(flight);
      setShow(true);
    }


    function setPage(page){
        if (page >=1 && page <= numPages){
            console.log("Setting page to " + page);
            setCurrentPage(page);
        } else {
            console.log("Not setting page because page is out of range: " + page);
        }
    }

    function paginationClicked(event) {
        var itemClicked = parseInt(event.target.text);
        console.log("itemClicked: " + itemClicked);
        //var itemClicked = event.target.dataset.key;
        setPage(itemClicked);
      }

    console.log("Current page: " + currentPage);

    let paginationItems = [];
    for (let number = 1; number <= numPages; number++) {
        paginationItems.push(
            <Pagination.Item key={number} active={number === currentPage} onClick={(event) => paginationClicked(event)}>
                {number}
            </Pagination.Item>,
        );
    }



    return (
        <>
            <Container>
                <Pagination>
                    <Pagination.First onClick={() => setPage(1)} disabled={(currentPage == 1)}/>
                    <Pagination.Prev onClick={() => setPage(currentPage - 1)} disabled={currentPage == 1}/>
                    {paginationItems}
                    <Pagination.Next onClick={() => setPage(currentPage + 1)} disabled={currentPage == numPages}/>
                    <Pagination.Last onClick={() => setPage(numPages)} disabled={currentPage == numPages}/>
                </Pagination>
                {/* This creates a 2 column layout on an xs screen and a 5 column layout on a medium or larger screen.  Alternatively, we could use directives on the Col that set the number of columns to occupy in a 12 column system:  xs={6} md={2} */}
                <Row xs={2} md={5}>
                    {flights.map((flight) =>
                        <Col className="mb-3" key={flight.id}>
                            <FlightDetail flight={flight} fields={fields} showFlight={ShowFlight} />
                        </Col>
                    )}
                </Row>
            </Container>
            <FlightModal show={show} flight={modalFlight} fields={fields} handleClose={handleClose} />

        </>
    );
}

export default FlightsSimple;