import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Stack } from 'react-bootstrap';
import FlightMethods from '../../utility/simple/FlightMethods';
import Form from 'react-bootstrap/Form';
import { useEffect, useState } from 'react';
//import { authToken } from '../../utility/Constants';
import { toast } from 'react-toastify';

//This version uses form submit and grabs the variables.  It's a bit simpler, since it doesn't use effects or state

function FlightModal({flight: flight, fields: fields, show, handleClose}) {

    const  dailiesBaseUrl = "https://localhost:5001/v1/api/Dailies";
    const formattedFlight = FlightMethods.getFormattedFlight(flight, fields);

    const updateFlight = (id, dailyValues) => {
        console.log("Updating flight " + id);
        const url = dailiesBaseUrl + '/UpdateDailyValues/' + id;
        const numRecords = 10;
        fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json' // Include if sending JSON data
            // Add other headers as needed
          },
          body: JSON.stringify(dailyValues)
        })
        .then(response => {
            if (!response.ok) {
              throw new Error(`HTTP error! Status: ${response.status}`);
            }
            return response.json();
          })
          .then(data => {
            console.log('Updating flight succeeded - Data:', data);
            // Handle the data
            handleClose();
            toast.success("Record was saved successfully");
          })
          .catch(error => {
            console.error('Error Updating flight:', error);
            toast.error("Record did not save successfully.  Error reported: " + error);
            // Handle errors
          });
    }

    function getLinecodeNumber() {
        const result = FlightMethods.getCodeshareField(flight, "linecode") + " " + FlightMethods.getCodeshareField(flight, "number");
        return result;
    }

    function setField(name, value, updatedFields) {

        var dailyValue = {field: name, instance: 1, value: value};
        updatedFields.push(dailyValue);
    }

    const handleSubmit = (event) => {
        event.preventDefault(); // Prevent default browser refresh

            console.log("Submit");
            console.log(event.target); 

            const updatedFields = [];
            setField("gate", event.target.gate.value, updatedFields);
            setField("city", event.target.city.value, updatedFields);
            setField("status", event.target.status.value, updatedFields);

            for (let i = 0; i < updatedFields.length; i++){
                const dv = updatedFields[i];
                if (!flight.dailyValues) flight.dailyValues = [];
                const refDv = flight.dailyValues.find(x => x.field == dv.field && x.instance == dv.instance);
                if (refDv)
                    refDv.value = dv.value;
                else {
                    flight.dailyValues.push(dv);
                }
            }

            // var newFlight = {id: flight.id, fields: updatedFields};
            console.log("Updating flight " + flight.id);
            console.log(updatedFields);
            updateFlight(flight.id, updatedFields);
    };



    return (
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>{getLinecodeNumber()}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={handleSubmit}>
                        <Form.Group className="mb-3" controlId="id">
                            <Form.Label>ID</Form.Label>
                            <Form.Control type="text" disabled={true} value={flight.id} />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="linecode_number">
                            <Form.Label>Flight</Form.Label>
                             <Form.Control type="text" disabled={true} defaultValue={formattedFlight.linecodeNumber} />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="gate">
                            <Form.Label>Gate</Form.Label>
                            <Form.Control type="text"  defaultValue={formattedFlight.gate} />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="city">
                            <Form.Label>City</Form.Label>
                            <Form.Control type="text"  defaultValue={formattedFlight.iata} />
                        </Form.Group>
                        <Form.Group className="mb-3" controlId="status">
                            <Form.Label>Status</Form.Label>
                            <Form.Control type="text"  defaultValue={formattedFlight.statusCode} />
                        </Form.Group>
                        <Button type="submit" variant="primary">
                            Save Changes
                        </Button>  
                        <span className="p-2" />                  
                        <Button variant="secondary" onClick={handleClose}>
                            Cancel
                        </Button>
                    </Form>
                </Modal.Body>
                <Modal.Footer>

                </Modal.Footer>
            </Modal>
    );
}

export default FlightModal;