import Card from 'react-bootstrap/Card';
import { Stack } from 'react-bootstrap';
import '../SomeCard.scss';
import FlightMethods from '../../utility/simple/FlightMethods';

function FlightDetail({flight, showFlight, fields}) {


var formattedFlight = FlightMethods.getFormattedFlight(flight, fields);


  return (
    <Card className="myCard" onClick={() => showFlight(flight)}>
      <Card.Body>
        <Card.Title>{flight.id} ({flight.adi}) {formattedFlight.linecode}{formattedFlight.number}</Card.Title>
        <Stack>
            <img src={formattedFlight.logo} />
            <div>{formattedFlight.iata}</div>
            <div>{formattedFlight.city}</div>
            <div>{formattedFlight.gate}</div>
            <div>{formattedFlight.status}</div>
            <div>{formattedFlight.schedule}</div>
        </Stack>
      </Card.Body>
    </Card>
  );
}

export default FlightDetail;